export default "<cn>\r\n#### 时间类控件\r\n时间类组件的 `value` 类型为 `moment` 对象，所以在提交服务器前需要预处理。\r\n</cn>\r\n\r\n<us>\r\n#### Time-related Controls\r\nThe `value` of time-related components is a `moment` object, which we need to pre-process it before we submit to server.\r\n</us>\r\n\r\n<template>\r\n  <a-form v-bind=\"formItemLayout\" :form=\"form\" @submit=\"handleSubmit\">\r\n    <a-form-item label=\"DatePicker\">\r\n      <a-date-picker v-decorator=\"['date-picker', config]\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"DatePicker[showTime]\">\r\n      <a-date-picker\r\n        v-decorator=\"['date-time-picker', config]\"\r\n        show-time\r\n        format=\"YYYY-MM-DD HH:mm:ss\"\r\n      />\r\n    </a-form-item>\r\n    <a-form-item label=\"MonthPicker\">\r\n      <a-month-picker v-decorator=\"['month-picker', config]\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"RangePicker\">\r\n      <a-range-picker v-decorator=\"['range-picker', rangeConfig]\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"RangePicker[showTime]\">\r\n      <a-range-picker\r\n        v-decorator=\"['range-time-picker', rangeConfig]\"\r\n        show-time\r\n        format=\"YYYY-MM-DD HH:mm:ss\"\r\n      />\r\n    </a-form-item>\r\n    <a-form-item label=\"TimePicker\">\r\n      <a-time-picker v-decorator=\"['time-picker', config]\" />\r\n    </a-form-item>\r\n    <a-form-item\r\n      :wrapper-col=\"{\r\n        xs: { span: 24, offset: 0 },\r\n        sm: { span: 16, offset: 8 },\r\n      }\"\r\n    >\r\n      <a-button type=\"primary\" html-type=\"submit\">\r\n        Submit\r\n      </a-button>\r\n    </a-form-item>\r\n  </a-form>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      formItemLayout: {\r\n        labelCol: {\r\n          xs: { span: 24 },\r\n          sm: { span: 8 },\r\n        },\r\n        wrapperCol: {\r\n          xs: { span: 24 },\r\n          sm: { span: 16 },\r\n        },\r\n      },\r\n      config: {\r\n        rules: [{ type: 'object', required: true, message: 'Please select time!' }],\r\n      },\r\n      rangeConfig: {\r\n        rules: [{ type: 'array', required: true, message: 'Please select time!' }],\r\n      },\r\n    };\r\n  },\r\n  beforeCreate() {\r\n    this.form = this.$form.createForm(this, { name: 'time_related_controls' });\r\n  },\r\n  methods: {\r\n    handleSubmit(e) {\r\n      e.preventDefault();\r\n      this.form.validateFields((err, fieldsValue) => {\r\n        if (err) {\r\n          return;\r\n        }\r\n\r\n        // Should format date value before submit.\r\n        const rangeValue = fieldsValue['range-picker'];\r\n        const rangeTimeValue = fieldsValue['range-time-picker'];\r\n        const values = {\r\n          ...fieldsValue,\r\n          'date-picker': fieldsValue['date-picker'].format('YYYY-MM-DD'),\r\n          'date-time-picker': fieldsValue['date-time-picker'].format('YYYY-MM-DD HH:mm:ss'),\r\n          'month-picker': fieldsValue['month-picker'].format('YYYY-MM'),\r\n          'range-picker': [rangeValue[0].format('YYYY-MM-DD'), rangeValue[1].format('YYYY-MM-DD')],\r\n          'range-time-picker': [\r\n            rangeTimeValue[0].format('YYYY-MM-DD HH:mm:ss'),\r\n            rangeTimeValue[1].format('YYYY-MM-DD HH:mm:ss'),\r\n          ],\r\n          'time-picker': fieldsValue['time-picker'].format('HH:mm:ss'),\r\n        };\r\n        console.log('Received values of form: ', values);\r\n      });\r\n    },\r\n  },\r\n};\r\n</script>\r\n";