export default "<cn>\r\n#### 自定义校验\r\n我们提供了 `validateStatus` `help` `hasFeedback` 等属性，你可以不需要使用 `Form.create` 和 `getFieldDecorator`，自己定义校验的时机和内容。\r\n1. `validateStatus`: 校验状态，可选 'success', 'warning', 'error', 'validating'。\r\n2. `hasFeedback`：用于给输入框添加反馈图标。\r\n3. `help`：设置校验文案。\r\n</cn>\r\n\r\n<us>\r\n#### Customized Validation\r\nWe provide properties like `validateStatus` `help` `hasFeedback` to customize your own validate status and message, without using `Form.create` and `getFieldDecorator`.\r\n1. `validateStatus`: validate status of form components which could be 'success', 'warning', 'error', 'validating'.\r\n2. `hasFeedback`: display feed icon of input control\r\n3. `help`: display validate message.\r\n</us>\r\n\r\n<template>\r\n  <a-form :label-col=\"labelCol\" :wrapper-col=\"wrapperCol\">\r\n    <a-form-item\r\n      label=\"Fail\"\r\n      validate-status=\"error\"\r\n      help=\"Should be combination of numbers & alphabets\"\r\n    >\r\n      <a-input id=\"error\" placeholder=\"unavailable choice\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Warning\" validate-status=\"warning\">\r\n      <a-input id=\"warning\" placeholder=\"Warning\" />\r\n    </a-form-item>\r\n    <a-form-item\r\n      label=\"Validating\"\r\n      has-feedback\r\n      validate-status=\"validating\"\r\n      help=\"The information is being validated...\"\r\n    >\r\n      <a-input id=\"validating\" placeholder=\"I'm the content is being validated\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Success\" has-feedback validate-status=\"success\">\r\n      <a-input id=\"success\" placeholder=\"I'm the content\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Warning\" has-feedback validate-status=\"warning\">\r\n      <a-input id=\"warning2\" placeholder=\"Warning\" />\r\n    </a-form-item>\r\n    <a-form-item\r\n      label=\"Fail\"\r\n      has-feedback\r\n      validate-status=\"error\"\r\n      help=\"Should be combination of numbers & alphabets\"\r\n    >\r\n      <a-input id=\"error2\" placeholder=\"unavailable choice\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Success\" has-feedback validate-status=\"success\">\r\n      <a-date-picker style=\"width: 100%\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Warning\" has-feedback validate-status=\"warning\">\r\n      <a-time-picker style=\"width: 100%\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Error\" has-feedback validate-status=\"error\">\r\n      <a-select default-value=\"1\">\r\n        <a-select-option value=\"1\">\r\n          Option 1\r\n        </a-select-option>\r\n        <a-select-option value=\"2\">\r\n          Option 2\r\n        </a-select-option>\r\n        <a-select-option value=\"3\">\r\n          Option 3\r\n        </a-select-option>\r\n      </a-select>\r\n    </a-form-item>\r\n    <a-form-item\r\n      label=\"Validating\"\r\n      has-feedback\r\n      validate-status=\"validating\"\r\n      help=\"The information is being validated...\"\r\n    >\r\n      <a-cascader :default-value=\"['1']\" :options=\"[]\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"inline\" style=\"margin-bottom:0;\">\r\n      <a-form-item\r\n        validate-status=\"error\"\r\n        help=\"Please select the correct date\"\r\n        :style=\"{ display: 'inline-block', width: 'calc(50% - 12px)' }\"\r\n      >\r\n        <a-date-picker style=\"width: 100%\" />\r\n      </a-form-item>\r\n      <span :style=\"{ display: 'inline-block', width: '24px', textAlign: 'center' }\">\r\n        -\r\n      </span>\r\n      <a-form-item :style=\"{ display: 'inline-block', width: 'calc(50% - 12px)' }\">\r\n        <a-date-picker style=\"width: 100%\" />\r\n      </a-form-item>\r\n    </a-form-item>\r\n    <a-form-item label=\"Success\" has-feedback validate-status=\"success\">\r\n      <a-input-number style=\"width: 100%\" />\r\n    </a-form-item>\r\n    <a-form-item label=\"Success\" has-feedback validate-status=\"success\">\r\n      <a-input allow-clear placeholder=\"with allowClear\" />\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Warning\" has-feedback validate-status=\"warning\">\r\n      <a-input-password placeholder=\"with input password\" />\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Error\" has-feedback validate-status=\"error\">\r\n      <a-input-password allow-clear placeholder=\"with input password and allowClear\" />\r\n    </a-form-item>\r\n  </a-form>\r\n</template>\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      labelCol: {\r\n        xs: { span: 24 },\r\n        sm: { span: 5 },\r\n      },\r\n      wrapperCol: {\r\n        xs: { span: 24 },\r\n        sm: { span: 12 },\r\n      },\r\n    };\r\n  },\r\n};\r\n</script>\r\n";