export default "<cn>\r\n#### 校验其他组件\r\n以上演示没有出现的表单控件对应的校验演示。\r\n</cn>\r\n\r\n<us>\r\n#### Other Form Controls\r\nDemonstration of validation configuration for form controls which are not shown in the demos above.\r\n</us>\r\n\r\n<template>\r\n  <a-form\r\n    id=\"components-form-demo-validate-other\"\r\n    :form=\"form\"\r\n    v-bind=\"formItemLayout\"\r\n    @submit=\"handleSubmit\"\r\n  >\r\n    <a-form-item label=\"Plain Text\">\r\n      <span class=\"ant-form-text\">\r\n        China\r\n      </span>\r\n    </a-form-item>\r\n    <a-form-item label=\"Select\" has-feedback>\r\n      <a-select\r\n        v-decorator=\"[\r\n          'select',\r\n          { rules: [{ required: true, message: 'Please select your country!' }] },\r\n        ]\"\r\n        placeholder=\"Please select a country\"\r\n      >\r\n        <a-select-option value=\"china\">\r\n          China\r\n        </a-select-option>\r\n        <a-select-option value=\"usa\">\r\n          U.S.A\r\n        </a-select-option>\r\n      </a-select>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Select[multiple]\">\r\n      <a-select\r\n        v-decorator=\"[\r\n          'select-multiple',\r\n          {\r\n            rules: [\r\n              { required: true, message: 'Please select your favourite colors!', type: 'array' },\r\n            ],\r\n          },\r\n        ]\"\r\n        mode=\"multiple\"\r\n        placeholder=\"Please select favourite colors\"\r\n      >\r\n        <a-select-option value=\"red\">\r\n          Red\r\n        </a-select-option>\r\n        <a-select-option value=\"green\">\r\n          Green\r\n        </a-select-option>\r\n        <a-select-option value=\"blue\">\r\n          Blue\r\n        </a-select-option>\r\n      </a-select>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"InputNumber\">\r\n      <a-input-number v-decorator=\"['input-number', { initialValue: 3 }]\" :min=\"1\" :max=\"10\" />\r\n      <span class=\"ant-form-text\">\r\n        machines\r\n      </span>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Switch\">\r\n      <a-switch v-decorator=\"['switch', { valuePropName: 'checked' }]\" />\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Slider\">\r\n      <a-slider\r\n        v-decorator=\"['slider']\"\r\n        :marks=\"{ 0: 'A', 20: 'B', 40: 'C', 60: 'D', 80: 'E', 100: 'F' }\"\r\n      />\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Radio.Group\">\r\n      <a-radio-group v-decorator=\"['radio-group']\">\r\n        <a-radio value=\"a\">\r\n          item 1\r\n        </a-radio>\r\n        <a-radio value=\"b\">\r\n          item 2\r\n        </a-radio>\r\n        <a-radio value=\"c\">\r\n          item 3\r\n        </a-radio>\r\n      </a-radio-group>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Radio.Button\">\r\n      <a-radio-group v-decorator=\"['radio-button']\">\r\n        <a-radio-button value=\"a\">\r\n          item 1\r\n        </a-radio-button>\r\n        <a-radio-button value=\"b\">\r\n          item 2\r\n        </a-radio-button>\r\n        <a-radio-button value=\"c\">\r\n          item 3\r\n        </a-radio-button>\r\n      </a-radio-group>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Checkbox.Group\">\r\n      <a-checkbox-group\r\n        v-decorator=\"['checkbox-group', { initialValue: ['A', 'B'] }]\"\r\n        style=\"width: 100%;\"\r\n      >\r\n        <a-row>\r\n          <a-col :span=\"8\">\r\n            <a-checkbox value=\"A\">\r\n              A\r\n            </a-checkbox>\r\n          </a-col>\r\n          <a-col :span=\"8\">\r\n            <a-checkbox disabled value=\"B\">\r\n              B\r\n            </a-checkbox>\r\n          </a-col>\r\n          <a-col :span=\"8\">\r\n            <a-checkbox value=\"C\">\r\n              C\r\n            </a-checkbox>\r\n          </a-col>\r\n          <a-col :span=\"8\">\r\n            <a-checkbox value=\"D\">\r\n              D\r\n            </a-checkbox>\r\n          </a-col>\r\n          <a-col :span=\"8\">\r\n            <a-checkbox value=\"E\">\r\n              E\r\n            </a-checkbox>\r\n          </a-col>\r\n        </a-row>\r\n      </a-checkbox-group>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Rate\">\r\n      <a-rate v-decorator=\"['rate', { initialValue: 3.5 }]\" allow-half />\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Upload\" extra=\"longgggggggggggggggggggggggggggggggggg\">\r\n      <a-upload\r\n        v-decorator=\"[\r\n          'upload',\r\n          {\r\n            valuePropName: 'fileList',\r\n            getValueFromEvent: normFile,\r\n          },\r\n        ]\"\r\n        name=\"logo\"\r\n        action=\"/upload.do\"\r\n        list-type=\"picture\"\r\n      >\r\n        <a-button> <a-icon type=\"upload\" /> Click to upload </a-button>\r\n      </a-upload>\r\n    </a-form-item>\r\n\r\n    <a-form-item label=\"Dragger\">\r\n      <div class=\"dropbox\">\r\n        <a-upload-dragger\r\n          v-decorator=\"[\r\n            'dragger',\r\n            {\r\n              valuePropName: 'fileList',\r\n              getValueFromEvent: normFile,\r\n            },\r\n          ]\"\r\n          name=\"files\"\r\n          action=\"/upload.do\"\r\n        >\r\n          <p class=\"ant-upload-drag-icon\">\r\n            <a-icon type=\"inbox\" />\r\n          </p>\r\n          <p class=\"ant-upload-text\">\r\n            Click or drag file to this area to upload\r\n          </p>\r\n          <p class=\"ant-upload-hint\">\r\n            Support for a single or bulk upload.\r\n          </p>\r\n        </a-upload-dragger>\r\n      </div>\r\n    </a-form-item>\r\n\r\n    <a-form-item :wrapper-col=\"{ span: 12, offset: 6 }\">\r\n      <a-button type=\"primary\" html-type=\"submit\">\r\n        Submit\r\n      </a-button>\r\n    </a-form-item>\r\n  </a-form>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data: () => ({\r\n    formItemLayout: {\r\n      labelCol: { span: 6 },\r\n      wrapperCol: { span: 14 },\r\n    },\r\n  }),\r\n  beforeCreate() {\r\n    this.form = this.$form.createForm(this, { name: 'validate_other' });\r\n  },\r\n  methods: {\r\n    handleSubmit(e) {\r\n      e.preventDefault();\r\n      this.form.validateFields((err, values) => {\r\n        if (!err) {\r\n          console.log('Received values of form: ', values);\r\n        }\r\n      });\r\n    },\r\n    normFile(e) {\r\n      console.log('Upload event:', e);\r\n      if (Array.isArray(e)) {\r\n        return e;\r\n      }\r\n      return e && e.fileList;\r\n    },\r\n  },\r\n};\r\n</script>\r\n<style>\r\n#components-form-demo-validate-other .dropbox {\r\n  height: 180px;\r\n  line-height: 1.5;\r\n}\r\n</style>\r\n";